import props from './computed';
import { addUnit } from '../common/utils/add-unit';
import styleUtil from '../common/utils/style';
import { isNotInUni } from '../common/utils/utils';

import PressIconPlus from '../press-icon-plus/press-icon-plus.vue';
import PressTransition from '../press-transition/press-transition.vue';

import InnerImage from '../image/index.vue';



export default {
  name: 'PressImage',
  options: {
    virtualHost: true,
    styleIsolation: 'shared',
  },
  components: {
    PressIconPlus,
    PressTransition,

    InnerImage,

  },
  mixins: [props],
  emits: [
    'click',
    'error',
    'load',
  ],
  data() {
    return {
      // 
      isError: false,
      // 
      loading: true,
      // 
      opacity: 1,
      // props
      durationTime: this.duration,
      // png
      backgroundStyle: {},
      // fade
      show: false,

      isNotInUni: isNotInUni(),
    };
  },
  computed: {
    wrapStyle() {
      const style = {};
      style.width = addUnit(this.width);
      style.height = addUnit(this.height);

      // 
      style.borderRadius = this.round ? '10000px' : addUnit(this.radius);

      //  hidden
      style.overflow = this.radius > 0 ? 'hidden' : 'visible';

      const res = styleUtil([
        {
          ...style,
          ...this.backgroundStyle,
        },
        this.customStyle,
      ]);

      return res;
    },
  },
  watch: {
    src: {
      immediate: true,
      handler(n) {
        if (!n) {
          // null''falseundefined
          this.isError = true;
        } else {
          this.isError = false;
          this.loading = true;
        }
      },
    },
  },
  mounted() {
    this.show = true;
  },
  methods: {
    addUnit,
    // 
    onClick() {
      this.$emit('click');
    },
    // 
    onErrorHandler(err) {
      this.loading = false;
      this.isError = true;
      this.$emit('error', err);
    },
    // loading
    onLoadHandler(event) {
      this.loading = false;
      this.isError = false;
      this.$emit('load', event);
      this.removeBgColor();
    },
    // 
    removeBgColor() {
      // png
      this.backgroundStyle = {
        backgroundColor: 'transparent',
      };
    },
  },
};
